.res-button{
    width: 150px;
    height: 60;
    padding:20px 10px;
    margin: 50px 0;
    text-align: center;
    border-radius: 6px;
    box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
    background: linear-gradient(145deg, #e2e8ec, #ffffff);
    align-self: flex-start;
}
.res-button:hover{
    color:#FF014F;
    transition: color .2s ease-in ;
}