.contact-section{
    width: 100vw;
    /* height: 100vh;
     */
     justify-content: center;
    align-items: center;
    padding: 100px 0;
    flex-direction: column;
    box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
    background: linear-gradient(145deg, #e2e8ec, #ffffff);
    /* background-image:linear-gradient(145deg, #e2e8ec46, #ffffff2d)  ,url(../images/contact.jpg); */
}
.contact-details{
    justify-content: center;
    align-items: center;
    width: 1335px;
    margin-top: 50px;
}
form input{
    padding: 11px 14px;
    /* background: #2E2E2E; */
    background: linear-gradient(145deg, #e2e8ec, #ffffff);
    box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
}
input:nth-child(1){
    margin-right: 78px;
}
input:nth-child(3){
    width: 99%;
    margin: 35px 0;
}
form textarea{
    /* background: #2E2E2E; */
    background: linear-gradient(145deg, #e2e8ec, #ffffff);
    box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
    padding: 11px 14px;
}
form button{
    padding: 14px 32px;
    background: #FF014F;
    margin-top: 35px;
    font-size: 14px;
    color: white;
}