.static-cards{
    flex-direction: column;
    height:auto;
    
    /* width:35%; */
    justify-content: space-between;
    /* padding:40px; */
    border-Radius:15px;
    width:240px;
    height:240px;
    padding:40px 40px 10px 40px;
    /* box-shadow: 5px 5px 19px #1c1e22, -5px -5px 19px #262a2e; */
    /* background: linear-gradient(145deg, #1e2024, #23272b); */
    /* box-shadow:5px 5px 15px #42454979, -5px -5px 15px #ffffffaf; */
    /* background:  linear-gradient(145deg, #e2e8ecab, #ffffff4d); */
    box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
    background: linear-gradient(145deg, #e2e8ec, #ffffff);
}
.static-value{
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    align-self:center;
    align-items:center;
    margin-bottom:20px;
    font-Size:38px;
    font-weight:bold;
    /* color:#E699B8; */
    color:#FF014F;
    padding:20px 10px;
    background: linear-gradient(145deg, #CED0D4, #ffffff);
    /* background:#181a1d; */
    /* background: linear-gradient(to bottom, #757f9a, #d7dde8); */
    /* background: linear-gradient(to right, #757f9a, #d7dde8); */
    /* // boxShadow="inset 21px 21px 19px #181a1d, inset -21px -21px 19px #202225;; */
    /* box-shadow:10px 10px 19px #1c1e22, -10px -10px 19px #262a2e; */
    box-shadow:5px 5px 15px #42454952, -5px -5px 15px #ffffffaf;
    border-radius:15px;
}
.static-line{
    /* border:.5px solid #181a1d; */
    border:.5px solid #3c3e41;
    box-shadow:10px 10px 19px #1c1e22, -10px -10px 19px #262a2e;
}
.static-dis{
    align-self:center;
    align-items:center;
   
    font-size:12px;
    color:#3c3e41;
}